.App {
  text-align: center;
}

input[type=color] {
  border: none;
}

html {
	height: 100%;
}

body {
	overflow: hidden;
 	height: inherit;
}

.cursor {
	display: none;
	margin: auto;
	width: 16px;
	height: 16px;
	background: white;
	border-radius: 50%;
	z-index: 5;
	pointer-events: none;
	position: fixed;
	opacity: .85;
	border: 1px solid #000000;
}

canvas {
	cursor: none;
}

